export default {
	data() {
		return {
			dataList: []
		};
	},
	created() {
		this.getPageList();
	},
	methods: {
		async getPageList() {
			const res = await this.request.apiUserQueryAll();
			if (res.code === this.IS_OK) {
				this.dataList = res.data;
			}
		},
		addBtnClick() {
			uni.navigateTo({
				url: `/pages/userAdd/userAdd`
			});
		},
		handleClickDetails(id) {
			uni.navigateTo({
				url: `/pages/userDetails/userDetails?id=${id}`
			});
		}
	}
};
